import React from 'react';
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import HomeIcon from '@material-ui/icons/Home';
// import EventIcon from '@material-ui/icons/Event';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import CallIcon from '@material-ui/icons/Call';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';
// import projInfo from '../ProjConfig.json';
import { Link } from "react-router-dom";



function Footer(props) {
    return (
        <div className="footerLinks">
            <Navbar className="centre" fixed="bottom" bg="dark" variant="dark">
                <Nav >
                    <Link value="Home" className="iconRightSpacing nav-link " to="/"><span><HomeIcon fontSize="small" /></span><br />Home</Link>
                    <Link value="Gallery" className="iconRightSpacing nav-link " to="/gallery"><span><PhotoLibraryIcon fontSize="small" /></span><br />Gallery</Link>
                    <Link value="Offers" className="iconRightSpacing nav-link " to="/offers"><span><LocalOfferIcon fontSize="small" /></span><br />Offers</Link>
                    <Link value="Blogs" className="iconRightSpacing nav-link " to="/blogs"><span><LibraryBooksIcon fontSize="small" /></span><br />Blogs</Link>
                    <Link value="Contact" className="iconRightSpacing nav-link " to="/contact"><span><CallIcon fontSize="small" /></span><br />Contact</Link>
                </Nav>
            </Navbar>
        </div>
    )
}

export default Footer;
