import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import projInfo from '../../../ProjConfig.json';


function Pictureslider() {
    const imagesList = projInfo.homePage.homePageCaroselimages;
    const carouselList = imagesList.map((image, i) => {
        return (
            <Carousel.Item key={i}>
                <img className="d-block w-100" src={image.img} alt={image.altName} />
                <Carousel.Caption>
                    <h3>{image.captionHeading}</h3>
                    <p>{image.captionDescription}</p>
                </Carousel.Caption>
            </Carousel.Item>
        )
    });
    return (
        <div>
            <Carousel>
                {carouselList}
            </Carousel>
        </div>
    )
}



export default Pictureslider;