import React from 'react';
import './Style.css';
import Header from './Application/Header'
import Footer from './Application/Footer'
import Browser from './Application/Browser/Browser'
import { BrowserRouter as Router } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router>
        <Header ></Header>
        <Browser></Browser>
        <Footer></Footer>
      </Router>
    </div>
  );
}

export default App;
