import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import HelpIcon from '@material-ui/icons/Help';
import ShoppingBasket from '@material-ui/icons/ShoppingBasket';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Kids from './KidsGalleryTab';

// import SvgIcon from '@material-ui/icons/Phone';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-force-tabpanel-${index}`}
            aria-labelledby={`scrollable-force-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `scrollable-force-tab-${index}`,
        'aria-controls': `scrollable-force-tabpanel-${index}`,
    };
}

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        width: '100%',
        backgroundColor: theme.palette.background.paper,
    },
}));

function Gallery() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleTabChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className="topPadding">
            <h3 className="pageTitle">G A L L E R Y</h3>

            <div className={classes.root}>
                <AppBar position="static" color="default">
                    <Tabs
                        value={value}
                        onChange={handleTabChange}
                        variant="scrollable"
                        scrollButtons="on"
                        indicatorColor="primary"
                        textColor="primary"
                        aria-label="scrollable force tabs example"
                    >
                        <Tab label="Kids" icon={<PhoneIcon />} {...a11yProps(0)} />
                        <Tab label="BirthDays" icon={<FavoriteIcon />} {...a11yProps(1)} />
                        <Tab label="Pre-Wedding" icon={<PersonPinIcon />} {...a11yProps(2)} />
                        <Tab label="Wedding" icon={<HelpIcon />} {...a11yProps(3)} />
                        <Tab label="Portfolio" icon={<ShoppingBasket />} {...a11yProps(4)} />

                    </Tabs>
                </AppBar>
                <TabPanel value={value} index={0}>
                   <Kids></Kids>
                </TabPanel>
                <TabPanel value={value} index={1}>
                    Item Two
      </TabPanel>
                <TabPanel value={value} index={2}>
                    Item Three
      </TabPanel>
                <TabPanel value={value} index={3}>
                    Item Four
      </TabPanel>
                <TabPanel value={value} index={4}>
                    Item Five
      </TabPanel>

            </div>
        </div>
    )
}



export default Gallery;
