import React, { useState } from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
// import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { Link } from "react-router-dom";
import projInfo from '../ProjConfig.json';

function Header(props) {
    const topBarLogo = projInfo.projLogo1;
    const topBarTitle = projInfo.projTitle;
    // const topMenuBasr = projInfo.topMenuBar;
    // const topMenuDeactive = projInfo.topMenuDeactive;
    const [Show, setShow] = useState("");
    // const ActiveClass = useState(topMenuDeactive);
    // const topMenuBarItems = topMenuBar.map((item) => {
    //     return (
    //         <Link key={item.toString()} value={item} onClick={toggleMenuActive.bind(this)} className={"nav-link " + ActiveClass[item]} to={"/" + item.toLowerCase()}>{item}</Link>)
    // })

    function toggleMenuActive(e) {
        // var menu = e.target.innerHTML;
        if (Show !== "show") {
            setShow("show")
        }
        else {
            // const Keyssss = Object.keys(ActiveClass);

            // Keyssss.forEach(element => {
            //     if (menu === element) {
            //         ActiveClass[element] = "active";
            //     }
            //     else if (menu !== element) {
            //         ActiveClass[element] = "notactive";
            //     }
            // });
            setShow("")
        }
    }

    return (
        <div>
            <Navbar fixed="top" collapseOnSelect expand="lg" bg="dark" variant="dark" >
                <Navbar.Brand href="/"><img className="topBarNav" src={topBarLogo} alt="logo"></img></Navbar.Brand>
                <Navbar.Brand href="/">{topBarTitle}</Navbar.Brand>
                <button onClick={toggleMenuActive} aria-controls="responsive-navbar-nav" type="button" aria-label="Toggle navigation" className="navbar-toggler"><span className="navbar-toggler-icon"></span></button>
                <Navbar.Collapse className={Show}>
                <Nav className="ml-auto">
                    <Link value="Home" className=" nav-link " to="/" onClick={toggleMenuActive.bind(this)}>Home</Link>
                    <Link value="About" className=" nav-link " to="/about" onClick={toggleMenuActive.bind(this)}>About</Link>
                    <Link value="Gallery" className=" nav-link " to="/gallery" onClick={toggleMenuActive.bind(this)}>Gallery</Link>
                    <Link value="Offers" className=" nav-link " to="/offers" onClick={toggleMenuActive.bind(this)}>Offers</Link>
                    <Link value="Blogs" className=" nav-link " to="/blogs" onClick={toggleMenuActive.bind(this)}>Blogs</Link>
                    <Link value="Booking" className=" nav-link " to="/booking" onClick={toggleMenuActive.bind(this)}>Booking</Link>
                    <Link value="Contact" className=" nav-link " to="/contact" onClick={toggleMenuActive.bind(this)}>Contact</Link>
                </Nav>
                </Navbar.Collapse>

                
            </Navbar>
        </div >
    )
}

export default Header;
